@import url(https://fonts.googleapis.com/css2?family=Wallpoet&display=swap" rel="stylesheet);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.navbar {
    background: #121212;
    height: 60px;
}

.navbar a {
    color: white;
    font-family: 'Wallpoet', Arial, Helvetica, serif, sans-serif;
    text-decoration: none;
    margin: 10px 5px;
}  

.navbar a:hover {
    color: #5bc0de;
}

.navbar .scrolldown a {
	position: absolute;
	bottom: 30px;
	left: 50%;
	margin-left: -29px;
	color: #fff;
	display: block;
	height: 42px;
	width: 42px;
	font-size: 42px;
	line-height: 42px;
	color: #fff;
	border-radius: 100%;
	transition: all .3s ease-in-out;
}
.header {
    position: relative;
    height: 630px;
    min-height: 500px;
    width: 100%;
    background: #121212;
    background-size: cover;
    -webkit-background-size: cover;
    text-align: center;
    overflow: hidden;
}

.profilepic {
    display: inline-block;
    margin-top: 20px;
    margin-bottom: 20px;
    width: 220px;
    height: 100%;
    position: relative;
    overflow: hidden;
    border-radius: 100%;
    border: solid 4px #fff;
}

.btn {
    padding: 20px;
    margin: 20px;
}

.btn:hover {
    padding: 20px;
    margin: 20px;
}

.social,
.fa-github,
.fa-linkedin {
    margin: 5px;
}

.social a {
    color: #fff;
}

.social a:hover {
    color: #5bc0de;
}
#about {
    position: relative;
    height: 600px;
    min-height: 500px;
    width: 100%;
    background: url(/reactPortfolio/static/media/joeking-36.faf6197e.jpg) no-repeat top center;
    background-size: cover;
    -webkit-background-size: cover;
    text-align: center;
    overflow: hidden;
}

.aboutme {
    position: relative;
    padding: 70px;
    font-size: 18px;
}

.contact a {
    color: #fff;
}
#portfolio {
    height: 300px;
    width: 100%;
    background: rgb(220, 220, 220);
    background-size: cover;
    -webkit-background-size: cover;
    overflow: hidden;
}

.image {
    opacity: 1;
    display: block;
    width: 100%;
    height: 300px;
    transition: .5s ease;
    -webkit-backface-visibility: hidden;
            backface-visibility: hidden;
}

#portfolio .card {
    width: 200px;
    height: 300px
}

.middle {
    transition: ease;
    opacity: 0;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%);
    text-align: center;
}

.card:hover .image {
    opacity: 0.5;
}

.card:hover .middle {
    background-color: #434343;
    width: 100%;
    height: 100%;
    opacity: 0.85;
}

.text {
    opacity: 1;
    color: white;
    font-size: auto;
    padding: 16px 32px;
}

#resume {
    position: relative;
    height: 1275px;
    min-height: 500px;
    width: 100%;
    background: #fff;
    background-size: cover;
    -webkit-background-size: cover;
    text-align: center;
    overflow: hidden;
}

#education {
    text-decoration: underline;
    -webkit-text-decoration-color: #5bc0de;
            text-decoration-color: #5bc0de;
    line-height: 20px;
    margin: 5px;
    padding: 10px;
}

#school {
    line-height: 30px;
}

.w3-border {
    padding: 10px;
    border: solid 3px #121212;
}

.w3-light-grey {
    border: solid thin #121212;
}

.w3-container {
    width: 65%;
}

#contact {
    position: relative;
    height: 630px;
    min-height: 500px;
    width: 100%;
    background: #121212;
    background-size: cover;
    -webkit-background-size: cover;
    text-align: center;
    overflow: hidden;
}

.form-group {
    margin-top: 20px;
}

input {
    width: 100%;
    padding: 12px 20px;
    margin: 8px 0;
}

textarea {
    width: 100%;
    height: 200px;
    padding: 12px 20px;
    margin: 8px 0;
}

button {
    background: #121212;
    border: none;
    color: #fff;
    padding: 15px 30px;
    border-radius: 4px;
}

button:hover {
    background: #fff;
    border: none;
    color: #121212;
    padding: 15px 30px;
}
.footer {
    background: #121212;
    background-size: cover;
    -webkit-background-size: cover;
    text-align: center;
    overflow: hidden;
}

a {
    color: #fff;
}

a:hover {
    color: #5bc0de;
}
