#resume {
    position: relative;
    height: 1275px;
    min-height: 500px;
    width: 100%;
    background: #fff;
    background-size: cover;
    -webkit-background-size: cover;
    text-align: center;
    overflow: hidden;
}

#education {
    text-decoration: underline;
    text-decoration-color: #5bc0de;
    line-height: 20px;
    margin: 5px;
    padding: 10px;
}

#school {
    line-height: 30px;
}

.w3-border {
    padding: 10px;
    border: solid 3px #121212;
}

.w3-light-grey {
    border: solid thin #121212;
}

.w3-container {
    width: 65%;
}
